/* Баннер */
.banner {
   display: block;
   position: relative;
   cursor: pointer;
   width: 100%;
   height: 325px;
   border-radius: 40px;
   overflow: hidden;
   border-width: 1px;
   border-style: solid;
   border-color: transparent;
   box-sizing: border-box;
}

.banner img {
   object-fit: cover;
   width: 100%;
   height: 100%;
}

.bannerMin {
   height: 450px;
}

.bannerContainer {
   position: absolute;
   display: flex;
   align-items: flex-end;
   width: calc(100% - 80px);
   height: calc(100% - 80px);
   padding: 40px;
}

.btn {
   display: flex;
   align-items: center;
   border-radius: 40px;
   background: #0D4CD3;
   padding: 8px 24px;
   color: white;
}

.btn img {
   margin-left: 4px;
   width: 24px;
   height: 24px;
}

.btn:hover {
   box-shadow: 1px 1px 10px rgba(12, 11, 11, 0.5);
}