.container {
   display: flex;
   flex-direction: column;
   justify-content: space-between;
   padding: 24px;
   border-radius: 40px;
   border: 1px solid #EDEDED;
   text-decoration: none;
   box-sizing: border-box;
}

.imgContainer {
   width: 277px;
   height: 160px;
   border-radius: 20px;
   overflow: hidden;
}

.imgContainer img {
   object-fit: cover;
   width: 100%;
   height: 100%;
}

.title {
   width: 277px;
   margin-top: 20px;
   color: #303030;
   font-size: 20px;
   font-style: normal;
   font-weight: 500;
   line-height: 24px;
}

.btn {
   width: 140px;
   display: flex;
   align-items: center;
   margin-top: 73px;
   padding: 8px 24px;
   border-radius: 40px;
   border: 1px solid #EDEDED;
   color: #303030;
   font-size: 16px;
   font-style: normal;
   font-weight: 400;
   line-height: 24px;
}
.btn:hover{
   box-shadow: 1px 1px 10px rgba(12, 11, 11, 0.5);
}

.btn img {
   margin-left: 4px;
   width: 24px;
   height: 24px;
}