@font-face {
   font-family: 'Inter';
   src: url("./assets/fonts/Inter/Inter-Regular.ttf") format("ttf");
}

* {
   font-family: 'Inter', sans-serif;
}

li {
   list-style: none;
}

input {
   border: none;
}

button {
   border: none;
   cursor: pointer;
   /* display: inline-block; */
}

.App {
   display: flex;
   flex-direction: column;
   min-height: 100vh;
}

.wrapper {
   flex: 1 0 auto;
}

.rowBlock {
   display: flex;
   justify-content: space-between;
}

/* Колонки */
.columnContainer {
   display: flex;
}

.columnLarge {
   width: calc(76% - 10px);
}

.columnSmal {
   margin-left: 20px;
   width: calc(24% - 10px);
}

/* Граница */
.borderMain{
   padding: 40px;
   border-radius: 40px;
   border: 1px solid #DFDFDF;
}

/* Карточка */
.cardContainer {
   display: flex;
   justify-content: space-between;
   
}

/* Текст */

.subTitle {
   font-size: 44px;
   font-style: normal;
   font-weight: 500;
   line-height: 48px;
}

/* Отступы */

.mt32 {
   margin-top: 32px;
}

.mt20 {
   margin-top: 20px;
}

.mt40 {
   margin-top: 40px;
}

.mt80 {
   margin-top: 80px;
}