/* Баннер */
.banner {
   display: block;
   position: relative;
   cursor: pointer;
   width: 325px;
   height: 400px;
   border-radius: 40px;
   overflow: hidden;
}

.banner img {
   object-fit: cover;
   width: 100%;
   height: 100%;
}

.bannerMin {
   height: 450px;
}

.bannerContainer {
   position: absolute;
   display: flex;
   justify-content: end;
   align-items: flex-end;
   width: calc(100% - 80px);
   height: calc(100% - 80px);
   padding: 40px;
}

.btn {
   border-radius: 50%;
   width: 68px;
   height: 68px;
   background: #ffff url('../../assets/icons/arrow_btn_link.svg') center center no-repeat;
   box-shadow: 0px 13px 33px 1px rgba(71, 71, 71, 0.24);
}

.btn:hover {
   box-shadow: 1px 1px 10px rgba(12, 11, 11, 0.5);
}