.title {
   color: #303030;
   font-size: 24px;
   font-style: normal;
   font-weight: 500;
   line-height: 28px;
}

.itemTextContainer {
   margin-top: 24px;
   display: flex;
   justify-content: space-between;
}

.column {
   width: 290px;
}

.item {
   position: relative;
   padding-left: 22px;
   color: #808080;
   font-size: 14px;
   font-style: normal;
   font-weight: 400;
   line-height: 20px;
   cursor: pointer;
}

.item:hover {
   text-shadow: 1px 1px 10px rgba(12, 11, 11, 0.5);
}

.item:not(:first-child) {
   margin-top: 12px;
}

.item::before {
   content: '•';
   position: absolute;
   left: 0;
}

.titleBanner {
   font-size: 24px;
   line-height: 28px;
   font-weight: 500;
   color: #303030;
}

.tel {
   margin-top: 24px;
   font-size: 18px;
   line-height: 24px;
   font-weight: 500;
   color: #303030;
}

.subTitleBanner {
   font-size: 12px;
   line-height: 16px;
   color: #808080;
}

.socialMediaContainer {
   margin-top: 24px;
   display: flex;
}

.socialMedia {
   cursor: pointer;
}

.socialMedia:not(:first-child) {
   margin-left: 14px;
}

.socialMedia img {
   object-fit: cover;
   width: 38px;
   height: 38px;
}

.bannerContainer {
   display: flex;
   flex-direction: column;
   justify-content: space-between;
}

.btn {
   margin-top: 126px;
   line-height: 24px;
   border-radius: 40px;
   background: radial-gradient(50% 47%, #f5982a, #eeee20);
   padding: 12px 24px;
   font-size: 16px;
   line-height: 24px;
   color: #8c7e00;
   text-align: center;
   cursor: pointer;
}

.btn:hover {
   box-shadow: 1px 1px 10px rgba(12, 11, 11, 0.5);
}

.linkContainer {
   margin: 24px 0;
   display: flex;
   justify-content: center;
}

.link {
   font-size: 14px;
   letter-spacing: -0.02em;
   line-height: 20px;
   color: #808080;
   text-decoration: none;
}

.link:not(:first-child) {
   margin-left: 24px;
}