.newsWrapper {
   display: flex;
   flex-wrap: wrap;
   justify-content: space-between;
}

.newsContainer {
   width: 277px;
   height: 302px;
   border-radius: 40px;
   border: 1px solid #EDEDED;
   padding: 24px;
   cursor: pointer;
}

.newsContainer:nth-child(n+4) {
   margin-top: 20px;
}

.preview {
   width: 277px;
   height: 160px;
   overflow: hidden;
   border-radius: 20px;
}

.preview img {
   width: 100%;
   height: 100%;
   object-fit: cover;
}

.filter {
   margin-top: 20px;
   color: #808080;
   font-size: 12px;
   font-style: normal;
   font-weight: 400;
   line-height: 20px;
}

.title {
   margin-top: 8px;
   color: #303030;
   font-size: 20px;
   font-style: normal;
   font-weight: 500;
   line-height: 22px;
   display: -webkit-box;
   -webkit-line-clamp: 3;
   -webkit-box-orient: vertical;
   overflow: hidden;
   text-overflow: ellipsis;
}

.moreDetails{
   margin-top: 8px;
   color: #808080;
   font-size: 12px;
   font-style: normal;
   font-weight: 400;
   line-height: 20px;
}

.showMore{
   margin: 40px auto 40px auto;
   display: flex;
   width: 325px;
   padding: 24px;
   justify-content: center;
   align-items: center;
   border-radius: 40px;
   border: 1px solid #B0B0B0;
   cursor: pointer;
}